import React from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';

import './code-execute-instruct.css'

export default function CodeExecuteInstruct(props) {
  
    const code = props.code
    const handleClose = props.handleClose
    const open = props.open
    
    const handleCloseAndJupyterLiteOpen = () => {
        const windowName = 'myJupyterLiteWindow'
        // Check if the window with the specified name exists // below function doesn't work as expected.
        let existingWindow = window.open('https://navinsubraman.github.io/my-jupyterlite/lab/index.html', windowName);

        // If the window doesn't exist or is closed, create a new one
        if (!existingWindow || existingWindow.closed) {
            existingWindow = window.open("https://navinsubraman.github.io/my-jupyterlite/lab/index.html", windowName);
        } else {
            // If the window is already open, focus it
            existingWindow.focus();
        }
    };

    const copyCodeToClipboard = () => {
        navigator.clipboard.writeText(code)
    }

    return (
    <div>
        <Dialog
        open={open}
        onClose={handleClose}
        maxWidth={"xl"}
        >
        <DialogTitle id="alert-dialog-title">
            {"Instruction to run the program"}
        </DialogTitle>
        <DialogContent>
            <img
                className='code-execute-instruct-gif' 
                src='https://user-images.githubusercontent.com/96062990/229962911-1bf173d2-7b0a-479a-95c7-8d7c3f34f55d.gif' alt='GIF: Instruction to run the program'></img>
        </DialogContent>
        <DialogActions>
            <Button onClick={copyCodeToClipboard}>Copy Code to Clipboard</Button>
            <Button onClick={handleCloseAndJupyterLiteOpen} autoFocus>Open JupyterLite</Button>
            <Button onClick={handleClose} autoFocus>Close</Button>
        </DialogActions>
        </Dialog>
    </div>
    );
    }