import React from 'react'
import './code-edit-run.css'
import LikeDislikeCheckboxes from './../../component/like-dislike/like-dislike'
import SimpleBackdrop from './../../component/backdrop/backdrop'
import CodeExecuteRepl from './../code-execute-repl/code-execute-repl'
import CodeExecuteInstruct from './../code-execute-instruct/code-execute-instruct'
import { postGenerateProgram, postGoodResults, postBadResults } from './../server-calls/server-calls'

// Import the Redux
import {useSelector, useDispatch} from 'react-redux'
import { UPDATE_PROMPT_VALUE, UPDATE_EDITOR_VALUE, UPDATE_FEEDBACK, UPDATE_SERVER_WAITING_STATUS } from './code-edit-run-slice'

// Import the Code Mirror Dependency
import { python } from '@codemirror/lang-python'
import { historyField } from '@codemirror/commands';
// // CodeMirror 3rd party package from https://uiwjs.github.io/react-codemirror/
import CodeMirror from '@uiw/react-codemirror'
//import { materialLight } from '@uiw/codemirror-theme-material'
//import { dracula } from '@uiw/codemirror-theme-dracula'
import { eclipse } from '@uiw/codemirror-theme-eclipse'

import TextareaAutosize from '@mui/base/TextareaAutosize';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import PlayCircleOutlineIcon from '@mui/icons-material/PlayCircleOutline';

function CodeEditRun() { 

    const editorState = useSelector((state) => state.codeEditRun.editorState)
    const editorValue = useSelector((state) => state.codeEditRun.editorValue)
    const editEnabled = useSelector((state) => state.codeEditRun.editEnabled)
    const codeFeedback = useSelector((state) => state.codeEditRun.codeFeedback)
    const promptValue = useSelector((state) => state.codeEditRun.promptValue)
    const promptPlaceholder = useSelector((state) => state.codeEditRun.promptPlaceholder)
    const promptUID = useSelector((state) => state.codeEditRun.promptUID)
    const serverWaiting = useSelector((state) => state.codeEditRun.serverWaiting)
    const [open, setOpen] = React.useState(false); // Using the React state without using Redux
    const dispatch = useDispatch()

    const editorExtensions = [python()]
    const stateFields = { history: historyField };

    const onPromptValueChange = React.useCallback((event) => {
        dispatch(UPDATE_PROMPT_VALUE(event.target.value))
    });

    const onCopyToClipboard = React.useCallback(() => {
        navigator.clipboard.writeText(promptPlaceholder)
    })

    const onGenerateEvent = React.useCallback((event) => {
        if(promptValue !== "") {
            dispatch(UPDATE_SERVER_WAITING_STATUS(true))
            postGenerateProgram(promptUID, promptValue, onRecievingProgramDetails)
        }
        dispatch(UPDATE_FEEDBACK("none"))
    });

    const onRecievingProgramDetails = React.useCallback((value) => {
        dispatch(UPDATE_EDITOR_VALUE({
            value: value,
            viewUpdateState: editorState
        }))
        dispatch(UPDATE_SERVER_WAITING_STATUS(false))
    })

    const onRunOpenEvent = React.useCallback((event) => {
        setOpen(true)
    });

    const onRunCloseEvent = React.useCallback((event) => {
        setOpen(false)
    });

    const onEditorValueChange = React.useCallback((value, viewUpdate) => {
        const state = JSON.stringify(viewUpdate.state.toJSON(stateFields));
        dispatch(UPDATE_EDITOR_VALUE({
                value: value,
                viewUpdateState: state
            }))
    });

    const onLikeChangeEvent = React.useCallback((event) => {
        if (codeFeedback !== "like" && editorValue !== "") {
            dispatch(UPDATE_FEEDBACK("like"))
            postGoodResults(promptUID, promptValue, editorValue)
        }
    });

    const onDislikeChangeEvent = React.useCallback((event) => {
        if (codeFeedback !== "dislike" && editorValue !== "") {
            dispatch(UPDATE_FEEDBACK("dislike"))
            postBadResults(promptUID, promptValue, editorValue)
        }
    });

    return(
        <div className='CodeEditorParent'>
            <p className='editor-description'>Enter your prompt for the measurement program that you want to develop below. To get good results, make sure you specify the pin name from the circuit and the operation that needs to happen on the pin clearly. Click "Generate" after entring the prompt.</p>
            <TextareaAutosize className='prompt'
                aria-label="prompt-area"
                minRows={7}
                maxRows={7}
                placeholder={promptPlaceholder}
                style={{ width: "98%"}}
                onChange={onPromptValueChange}
                value={promptValue}
                />
            <div className='CodeEditorHeader'>
                <IconButton title='copy the example to clipboard' onClick={onCopyToClipboard}><ContentCopyIcon htmlColor='#AAC8C7'/></IconButton>
                <Button variant="contained" onClick={onGenerateEvent} title='generate program'>Generate</Button>
                <IconButton color="primary" onClick={onRunOpenEvent} title='Click to run the program' disabled={!editorValue}><PlayCircleOutlineIcon /></IconButton>
                <LikeDislikeCheckboxes checkState={codeFeedback} onLikeChangeEvent={onLikeChangeEvent} onDislikeChangeEvent={onDislikeChangeEvent}/>
            </div>
            
            <div className='EditorAndConsole'>
                <p className='editor-description'>Below generated results may not be accurate and cannot be used in production or even for device testing trials without proper review from the measurement science engineer. The AI is trained to adopt <a href='https://navinatsoliton.pythonanywhere.com/static/functional_drivers.py' target='_blank' rel="noreferrer">this driver definition</a>. If you would like to provide a quick feedback on the generated program, click the 'like' or 'dislike' buttons above so the results can be used to improve the performance.</p>
                <div className='CodeEditor'>
                    <CodeMirror
                        value={editorValue}
                        placeholder="AI generated program will be displayed in here."
                        initialState={editorState
                            ?{
                                json: JSON.parse(editorState),
                                fields: stateFields
                            }:undefined
                        }
                        height="31rem"
                        theme={eclipse}
                        extensions={editorExtensions}
                        readOnly={!editEnabled}
                        basicSetup = {{lineNumbers: true, foldGutter: true, highlightActiveLine: false }}
                        onChange={onEditorValueChange}
                            />
                </div>
            </div>

            <SimpleBackdrop open={serverWaiting} message={"Generating program now"}/>
            <CodeExecuteInstruct open={open} handleClose={onRunCloseEvent} code={editorValue}></CodeExecuteInstruct>
        </div>
    )
}

export default CodeEditRun

// If need to call the Repl code
//<CodeExecuteRepl open={open} handleClose={onRunCloseEvent} code={editorValue}/>
